:root {
  --clr-cream: #A59678;
  --clr-dark-blue: #3B4050;
  --clr-gray: #6F737F;
  --clr-dark: #2E323F;
  --clr-white: #FFFFFF;
  /* font-sizes */
  --fs-900: rem(90);
  --fs-800: rem(36);
  --fs-700: rem(30);
  --fs-600: rem(24)
  --fs-500: rem(21);
  --fs-400: rem(18);
  --fs-300: rem(16);
  --fs-200: rem(14);
  /* font-families */
  --ff-carter: "Carter One", cursive;
  --ff-oswald: "Oswald", sans-serif;
  --ff-montserrat: "Montserrat", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-montserrat);
  line-height: 1.3;
  min-height: 100vh;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

a {
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
}
ul li {
  list-style-type: none;
}

img {
  max-width: 100%;
  display: block;
}

/* remove animations for people who've turned them off */
@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* Screen readers */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.text-uppercase {
  text-transform: uppercase;
}

.flex {
  display: flex;
}